html,
body,
.main {
  position: relative;
}

body {
  min-width: 600px;
  background: #fff;
  font-size: 14px;
  line-height: 1.4;
}

.main {
  margin: 0 auto;
  max-width: 1200px;
}

.row {
  margin: -40px -20px;
}

.row:after {
  content: " ";
  display: table;
  clear: both;
}

.col {
  position: relative;
  float: left;
  padding: 40px 20px;
  width: 50%;
}

@media only screen and (max-width: 979px) {
  .col {
    width: 100%;
  }
}

.col:nth-child(1):before {
  right: 0;
}

@media only screen and (max-width: 979px) {
  .col:nth-child(2) {
    border-top: 1px solid #eee;
  }
}

.col:nth-child(2):before {
  left: -1px;
}

.col:before {
  content: '';
  position: absolute;
  top: -20px;
  bottom: -20px;
  border-left: 1px solid #eee;
}

@media only screen and (max-width: 979px) {
  .col:before {
    display: none;
  }
}

.sm-container {
  margin: -20px;
}

.sm-section {
  padding: 40px 40px;
  margin: 20px auto;
  border: 1px solid #eee;
